(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension', ['ngRoute', 'ui.bootstrap', 'ngYoutubeEmbed']);

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .directive('navbarHeader', navbarHeader);

  /** @ngInject */
  function navbarHeader() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {},
      controller: NavbarController,
      controllerAs: 'nc',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($log, $rootScope, $uibModal, GenreModel) {
      var vm = this;
      vm.colour = GenreModel.getGenreColour();
      vm.openLoginForm = function () {
        $uibModal.open({
           animation: true,
           templateUrl: 'app/components/login/login.html',
           controller: 'LoginController',
           controllerAs: 'lc',
           resolve: {
             items: function () {
               return [];
             }
           }
        });
      };
      var temp = $rootScope.$on('genreColour', function(event, args) {
        vm.colour = args;
      });
      $log.info(temp);

    }
  }

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('MenuController', MenuController);

  /** @ngInject */
  function MenuController($log, $rootScope, GENRES, GenreModel) {
    var vm = this;
    vm.genreKeys = GenreModel.getGenres();
    vm.genreColour = GenreModel.getGenreColour();
    vm.selectedGenre = GenreModel.getGenre();

    vm.setGenre = setGenre;
    vm.genreColourByKey = genreColourByKey;

    function setGenre(key) {
      GenreModel.setGenre(key);
      vm.selectedGenre = GenreModel.getGenre();
      vm.genreColour = GenreModel.getGenreColour();
      $rootScope.$broadcast('genreColour', vm.genreColour);
    }

    function genreColourByKey(key) {
      return GenreModel.getGenreColourByKey(key);
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($scope, $uibModalInstance, $log) {
    var vm = this;
    vm.login =  function () {
      $log.info("login clicked");
      $uibModalInstance.close();
    };

    vm.cancel = function () {
      $log.info("Cancel clicked");
      $uibModalInstance.dismiss('cancel');
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('DetailController', DetailController);

  /** @ngInject */
  function DetailController(SelectedSong) {
      var vm = this;
      vm.selectedSong = SelectedSong[0];
  }
})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {
  }
})();

'use strict';
(function () {

    /**
     * All info and methods related to a GENRE.
     * @ngInject
     */
    var GenreModel = function (GENRES) {

        var selectedGenre = GENRES['rock'];

        return {
            getGenreColour: getGenreColour,
            setGenre: setGenre,
            getGenre: getGenre,
            getGenres: getGenres,
            getGenreColourByKey: getGenreColourByKey
        };

        function getGenres() {
          return Object.keys(GENRES);
        }

        function setGenre(key) {
          selectedGenre = GENRES[key];
        }

        function getGenreColour() {
            return selectedGenre[0].colour;
        }

        function getGenreColourByKey(key) {
            return GENRES[key][0].colour;
        }

        function getGenre() {
            return selectedGenre;
        }
    };

    angular.module('jukeboxcomprehension').factory('GenreModel', GenreModel);

})();

(function() {
  'use strict';

  angular
    .module('jukeboxcomprehension')
    .config(routeConfig);

  function routeConfig($routeProvider, $locationProvider) {
    $routeProvider
      .when('/songs/:GENRE/:ID/', {
        templateUrl: 'app/components/detail/detail.html',
        controller: 'DetailController',
        controllerAs: 'detail',
        resolve: {
          SelectedSong: function($route, $filter, GENRES) {
            return $filter('filter')(GENRES[$route.current.params.GENRE], { id: $route.current.params.ID });
          }
        }
      })
      .when('/', {
        templateUrl: 'app/components/menu/menu.html',
        controller: 'MenuController',
        controllerAs: 'menu'
      })
      .otherwise({
        redirectTo: '/'
      });
    $locationProvider.html5Mode(true);
  }

})();

